import React from "react";
import { HashRouter, Switch, Route, BrowserRouter } from "react-router-dom";
import { Theme } from "./theme";
import Adventures from "./screens/Adventures";
import AdventureDetail from "./screens/AdventureDetail";
import Index from "./screens/Index";
import Magazine from "./screens/Magazine";
import MagazineDetail from "./screens/MagazineDetail";
import Settings from "./screens/Settings";
import TabBar from "./components/TabBar";
import { contentPath } from "./utils";
import { PathUtils } from '@adobe/aem-spa-page-model-manager';
import './App.css';

const { REACT_APP_HOST_URI } = process.env;

export default function App() {

  const transformRoute = (path) => {
        // REACT_APP_HOST_URI = http://localhost:4502
        // contentPath = wknd-app/en/index
        let route = PathUtils.toAEMPath(path, 'https://author-p49018-e411369.adobeaemcloud.com/', 'wknd-app/en/index');
        console.log('route: ' + route);
        return route;
  };
  
  const AemSpaRouter = window.top.location.pathname.startsWith('/editor.html') ? BrowserRouter : HashRouter;

  return (
    <div id="app-container" style={styles.container}>
      <AemSpaRouter>
        <Switch>
          <Route path={transformRoute('/adventures')}>
            <Adventures />
          </Route>
          <Route path={transformRoute('/detail')}>
            <AdventureDetail />
          </Route>
          <Route path={transformRoute('/magazine/:id')}>
            <MagazineDetail />
          </Route>
          <Route path={transformRoute('/magazine')}>
            <Magazine />
          </Route>          
          <Route path={transformRoute('/settings')}>
            <Settings />
          </Route>
          <Route path={transformRoute('/')}>
            <Index />
          </Route>  
        </Switch>
        <TabBar />
      </AemSpaRouter>
    </div>
  );
}

const styles = {
  container: {
    position: "relative",
    backgroundColor: Theme.colors.background,
    paddingTop: "10px",
    color: Theme.colors.text,
  },
}