export const externalizePaths = (json, acceptsFn, rewriteFn) => {
    if (!acceptsFn) { acceptsFn = function() { return false; }}
    if (!rewriteFn) { rewriteFn = function(value) { return value; }}

    let data = _processModelJson(json, acceptsFn, rewriteFn);

    return data;
}

export const acceptContent = (json, key, value) => {
    
    const accept = value.match(/^\/content\/([^\0]*)(\.jpg|[^\0]jpeg|[^\0]png|[^\0]gif|[^\0]html)$/i);
    console.log(`[ ${accept != null} => ${value}]`);
    return accept;
}

const _processModelJson = (jsonObject, acceptsFn, rewriteFn) => {
    Object.keys(jsonObject).forEach(function (propertyName) {    
        if (jsonObject[propertyName] !== null && typeof jsonObject[propertyName] === 'object') {
            _processModelJson(jsonObject[propertyName], acceptsFn, rewriteFn);
            return;
        }
        
        if (typeof jsonObject[propertyName] === 'string' && 
                acceptsFn(jsonObject, propertyName, jsonObject[propertyName])) {
            jsonObject[propertyName] = rewriteFn(jsonObject[propertyName]);
        }
    });
    return jsonObject;
}
