import { ModelManager } from "@adobe/aem-spa-page-model-manager";
import { contentPath } from "../utils";
import { RemoteSpaModelClient } from "../utils/RemoteSpaModelClient";
import { acceptContent } from "./Externalizer";

const { REACT_APP_AUTHORIZATION, REACT_APP_HOST_URI } = process.env;

export const initModelManager = (path) => {

    const aemAuthorization = {
        basic: {
            user: REACT_APP_AUTHORIZATION?.split(':')[0],
            password: REACT_APP_AUTHORIZATION?.split(':')[1],
        }
    };

    ModelManager.initializeAsync({
        modelClient: new RemoteSpaModelClient(
            REACT_APP_HOST_URI,
            aemAuthorization,
            _transformRoute,
            acceptContent,
            _rewriteContentPaths
        ), 
        path: path
    });
}

function _rewriteContentPaths(value) {
    let originalValue = value;
    
    if (value.startsWith(contentPath) && value.match(/^\/content\/.*\.(html)$/)) {
        // This looks like a React app route
        value = value.substring((contentPath).length);
    } else if(REACT_APP_HOST_URI && value.match(/^\/content\/.*\.(jpg|jpeg|gif|png|wepb)$/)) {
        // Externalize images
        value = REACT_APP_HOST_URI + value;
    } 

    return value;
}

function _transformRoute(route) {
    console.log("Starting to process route transform: " + route);

    const AEM_CONTENT_PREFIX = '/content/wknd-app/en/';

    if (route === process.env.PUBLIC_URL + '.model.json') {
        route = AEM_CONTENT_PREFIX + 'index.model.json';
        //console.log("Route was  " + process.env.PUBLIC_URL + ".model.json transformed to: " + route);
    } else if (route.startsWith(process.env.PUBLIC_URL)) {
        route = route.replace(process.env.PUBLIC_URL, AEM_CONTENT_PREFIX);
        //console.log("Route started with " + process.env.PUBLIC_URL + " transformed to: " + route);

    } else if (!route.startsWith(AEM_CONTENT_PREFIX)) {
        
        if (route.startsWith('/')) {
            route = route.substring(1);
        }

        route = AEM_CONTENT_PREFIX + route;

        //console.log("Route did NOT start with " + AEM_CONTENT_PREFIX + " transformed to: " + route);
    } else {
        //console.log("No transformation necessary: " + route);
    }
        
    return route;
}